<template>
  <div class="container index-box">
	 
	  <div class="blind-box flex-between">
		 <div class="left">
			<div class="box-img">
			  <img src="@/assets/images/box.png" alt="">
			</div>
			<div class="input-box flex-between">
				<div class="text">Quantity</div>
				<el-input-number v-model="num" :min="1" :max="100"></el-input-number>
			</div>
			<div class="input-box flex-between">
              <div class="text">Price</div>
			  <el-dropdown trigger="click"  @command="changePay">
				<span class="el-dropdown-link">
					{{isWhitelist ? '0.00' : price}}{{payType == '1' ? 'BNB' : 'USDT'}}<i class="el-icon-arrow-down el-icon--right"></i>
				</span>
				<template #dropdown>
					<el-dropdown-menu>
					<el-dropdown-item :class="{'active': payType == '1'}" command="1">BNB</el-dropdown-item>
					<el-dropdown-item :class="{'active': payType == '2'}" command="2">USDT</el-dropdown-item>
					</el-dropdown-menu>
				</template>
				</el-dropdown>
			</div>
			<div class="btn-box flex-middle">
				<el-button class="flex-item" v-if="!address" @click="initWeb3" type="primary" round>Connect Wallet</el-button>
				<el-button class="flex-item" v-if="isWhitelist" @click="claim" type="primary" round>Claim</el-button>
				<el-button class="flex-item" v-else-if="payType == '1'" :loading="btnLoading" :disabled="btnLoading" type="primary" @click="buy" round>Buy</el-button>
			    <el-button class="flex-item" v-else-if="Number(allowance) <= 0" :loading="btnLoading" :disabled="btnLoading" type="primary" @click="approve" round>Approve USDT</el-button>
				<el-button class="flex-item" v-else :loading="btnLoading" :disabled="btnLoading" type="primary" @click="buyByUsdt" round>Buy</el-button>
			</div>
		</div>
		<div class="right">
		  <div class="item">
			<div class="title">Lucky Draw</div>
			<div class="desc">Unseal the blind box and you may be lucky to get a Holiday Series Classic Card. You will have a 25% chance of getting each of the following cards: Sunbathing, Selfie, Traveler, Dr.Strange.</div>
		  </div>
		  <div class="item">
			<div class="title">Additional Prizes</div>
			<div class="desc">Collect all 4 Classic Cards to get your additional 20% reward.</div>
		  </div>
		  <div class="item">
			<div class="title">Card Slot</div>
			<div class="desc">Put the Bunny in the corresponding card slot to start earning BP!</div>
		  </div>
		</div>
	  </div>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch} from "vue";
import web3Data from "@/utils/initWeb3";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import nftAbi from "@/utils/nftAbi";
import { ElMessage } from 'element-plus'
import BigNumber from "bignumber.js";
import { MaxUint256 } from "@ethersproject/constants";
import { useRouter } from "vue-router";
export default {
  setup () {
     const store = useStore();
	 const router = useRouter();
     const data = reactive({
       web3: computed(() => store.state.provider),
	   rate: computed(() => store.state.rate),
       address: computed(() => store.state.accounts[0]),
	   currentBalance: computed(() => store.state.currentBalance),
	   nftContract: null,
       usdtContract: null,
	   isWhitelist: false,
	   payType: '1',
	   avatars: [],
	   btnLoading: false,
	   drawer: false,
	   usdtPrice: 0,
	   ethPrice: 0,
	   allowance: 0,
	   num: 1,
	   price: computed(() => {
		 if(data.ethPrice && data.payType == '1') {
           return new BigNumber(data.ethPrice).multipliedBy(data.num).toFixed(3)
		 }
		 if(data.usdtPrice && data.payType == '2') {
           return new BigNumber(data.usdtPrice).multipliedBy(data.num).toFixed(3)
		 }
		 return '0.00'
	   }),
	   initContract: () => {
		 data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
		 data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
		 data.initData()
	   },
	   initData: () => {
		 data.getPayMethods()
		 data.getAllowance()
	   },
	   initWeb3: () => {
         web3Data.initWeb3()
       },
	   changePay: (val) => {
		 data.payType = val
	   },
	   handleClose: (done) => {
         
       },
	   getAllowance: async () => {
         let allowance = await data.usdtContract.methods.allowance(data.address, config.nftAddress).call()
		 data.allowance = allowance
		 console.log(allowance)
       },
	   approve: async () => {
        data.btnLoading = true
        try {
          await data.usdtContract.methods.approve(config.nftAddress, MaxUint256).send({from: data.address})
          ElMessage.success('Approve Success')
          data.getAllowance()
          data.btnLoading = false
        } catch (error) {
          ElMessage.error('Approve faild')
          data.btnLoading = false
        }
        
      },
	   getPayMethods: async () => {
		 let usdtPrice = await data.nftContract.methods.usdtPrice().call()
		 let ethPrice = await data.nftContract.methods.ethPrice().call()
		 let isWhitelist = await data.nftContract.methods.isWhitelist(data.address).call()
		 data.usdtPrice = new BigNumber(usdtPrice).dividedBy(10 ** 18).toFixed(3)
		 data.ethPrice = new BigNumber(ethPrice).dividedBy(10 ** 18).toFixed(3)
		 console.log(data.usdtPrice, data.ethPrice)
		 data.isWhitelist = isWhitelist
	   },
	   claim:  async () => {
		 try {
			await data.nftContract.methods.mint('1').send({from: data.address, value: 0})
			ElMessage.success('claim success');
			data.btnLoading = false	
			router.push({name: 'MyCollection'})
		 } catch (error) {
			ElMessage.error('claim faild');
			data.btnLoading = false	 
		 }  
	   },
	   buy: async () => {
		 if(!data.num) {
		  ElMessage.error('Purchase quantity is greater than 0');
		  return
		 }
		 let ethPrice = await data.nftContract.methods.ethPrice().call()
		 let payAmount = new BigNumber(ethPrice).multipliedBy(data.num).toFixed();
         if(Number(data.currentBalance) < Number(payAmount)) {
		   ElMessage.error('BNB Insufficient balance');
		  return
		 }
		 try {
		   let isWhitelist = await data.nftContract.methods.isWhitelist(data.address).call()
			let value = isWhitelist ? 0 : payAmount
			data.btnLoading = true
			await data.nftContract.methods.mint(data.num).send({from: data.address, value: value})
			ElMessage.success('buy success');
			data.btnLoading = false	
			router.push({name: 'MyCollection'})
		 } catch (error) {
			ElMessage.error('buy faild');
			data.btnLoading = false	 
		 }
		 
	   },
	   buyByUsdt: async () => {
		 if(!data.num) {
		  ElMessage.error('Purchase quantity is greater than 0');
		  return
		 }
		 let usdtPrice = await data.nftContract.methods.usdtPrice().call()
		 let payAmount = new BigNumber(usdtPrice).multipliedBy(data.num).toFixed();
		 let usdtBalance = data.usdtContract.methods.balanceOf(data.address).call()
		 if(Number(usdtBalance) < Number(payAmount)) {
		   ElMessage.error('USDT Insufficient balance');
		  return
		 }
		 if(Number(data.allowance) < Number(payAmount)) {
		   ElMessage.error('USDT Insufficient allowance');
		   return
		 }
		  try {
			data.btnLoading = true
			await data.nftContract.methods.mint(data.num).send({from: data.address})
			ElMessage.success('buy success');
			data.btnLoading = false
			router.push({name: 'MyCollection'})
		 } catch (error) {
			ElMessage.error('buy faild');
			data.btnLoading = false	 
		 }
	   }
     })
     if (data.address) {
		 data.initContract()
     }
     watch(() => data.address, (val) => {
       if (val) {
		data.initContract()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss">
.el-drawer .svg svg{
  width: 40px !important;
  height: 40px !important;
}
.el-dropdown-menu__item.active{
  color: #20CAA2;
}
