  border: none !important;
  width: 38px !important;
  height: 38px !important;
  color: #fff !important;
  font-size: 15px !important;
  font-weight: 700 !important;
}
.left [class*=" el-icon-"], [class^=el-icon-] {
   font-size: 13px !important;
  font-weight: 700 !important;
}
.left .el-input-number__decrease.is-disabled {
  background: rgba($color: #fff, $alpha: 0.2) !important;
  color: #fff !important;
}
.left .el-button{
  height: 54px !important;
  border-radius: 27px !important;
  font-size: 20px !important;
  font-weight: 600;
}
.input-box .el-dropdown-link{
  color: #20CAA2;
  font-weight: 700;
  font-size: 16px;
  .el-icon--right{
	color: #fff;
	font-weight: normal;
	font-size: 14px;
  }
}
</style>
