<script>
import { reactive, toRefs, computed } from "vue";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n"
import {useStore} from "vuex";
export default {
  setup() {
    const router = useRouter();
    const store = useStore();
    const data = reactive({
      themeMode: computed(() => store.state.themeMode),
      mobileShowMenu: false,
      web3: computed(() => store.state.provider),
      address: computed(() => store.state.accounts[0]),
      avatar: computed(() => store.state.avatar),
      show: false,
      showWallets: false,
      local: getLang().toLowerCase(),
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      handleSelect: () => {
        data.mobileShowMenu = false
      },
      visibleChange: (val) => {
        data.show = val
      },
      initWeb3: () => {
        web3Data.initWeb3()
      }
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss">
.header{
  max-width: 1200px;
  margin: 0 auto;
}
.header-box .language{
  justify-content: flex-end;
 
  cursor: pointer;
  .el-dropdown{
    color: #75809F;
    font-size: 16px;
  }
  .mobile-language {
    display: none;
  }
  .mobile-menu {
    display: none;
    height: 100%;
    .nav-icon {
      margin: 36px 0 0 10px;
      display: inline-block;
      position: relative;
      width: 20px;
      height: 2px;
      background-color: #DFE0E2;
      transition-property: background-color, transform;
      transition-duration: 300ms;
      &:before,
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: inherit;
        position: absolute;
        background:  #DFE0E2;
        transition-property: margin, transform;
        transition-duration: 300ms;
      }
      &:before {
        margin-top: -8px;
      }
      &:after {
        margin-top: 8px;
      }
      &.active {
        background: rgba(0, 0, 0, 0);
        &:before {
          margin-top: 0;
          transform: rotate(45deg);
        }
        &:after {
          margin-top: 0;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
.header-box .el-dropdown-menu__item.active{
  color: #0264C3;
}
.el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover {
